      filters:{
        time01:function (value) {
          var Y,M,D,month;
          var date = new Date(value * 1000);//10*1000131000
          Y = date.getFullYear();
          M = (date.getMonth()+1 < 10 ? (date.getMonth()+1) : date.getMonth()+1);
          D = date.getDate();
          return Y+'-'+M+'-'+D;
        },
        newDesc02:function (value) {
          var str = value.slice(0,22);
          str = str+'...';
          return str
        }
      },
